  var onConference = false;

  function initConference() {
    if (!onConference) {
      getPresenceActiveCalls().then(res => {
        var response = res.json();
        var pId = response.party_id;
        var tId = response.telephonySessionId;
        getConfVoiceToken(pId, tId).then(voiceToken => {
          startConferenceCall(voiceToken, pId, tId);
          onConference = true;
        });
      });
    }
  }


  function getPresenceActiveCalls() {
    var url = '/restapi/v1.0/account/~/extension/~/presence';
    return platform.post(url, {
      username: username
    });

  }

  function getConfVoiceToken(pId, tId) {
    return platform.post('/restapi/v1.0/account/~/extension/~/presence', {
      username: username
    }).then(res => {
      confSessionId = res.session_id;
      return res.voiceCallToken;
    });
  }

  function startConferenceCall(number, pId, tId) {
    var session = webPhone.userAgent.invite(number, {
      fromNumber: username
    });
    session.on('accepted', function() {
      onAccepted(session);
      console.log('Conference call started');
      bringIn(tId, pId)
        .then(response => {
          console.log('Adding call to conference succesful', response.json());
        })
        .catch(function(e) {
          console.error('Conference call failed', e.stack || e);
        });
    });
  }

  function bringIn(tId, pId) {
    var url = '/restapi/v1.0/account/accountId/telephony/sessions/' + confSessionId + '/parties/bring-in';
    return platform.post(url, {
      telephonySessionId: tId,
      partyId: pId
    });
  }



  /*FLUX FUNCTIONS*/

  function formatPhone(phone) {
    var num;
    if (phone.indexOf('@')) {
      num = phone.split('@')[0];
    }
    else {
      num = phone;
    }
    num = num.toString().replace(/[^0-9]/g, '');
    if (num.length === 10) {
      return '(' + num.substr(0, 3) + ') ' + num.substr(3, 3) + '-' + num.substr(6, 4);
    }
    else if (num.length === 11) {
      return '(' + num.substr(1, 3) + ') ' + num.substr(4, 3) + '-' + num.substr(7, 4);
    }
    else {
      return num;
    }
  }

  function startRingTone() {
    // @ts-ignore
    try {
      ringtone.play();
    }
    catch (e) {}
  }

  function stopRingTone() {
    try {
      ringtone.pause();
    }
    catch (e) {}
  }

  function startRingbackTone() {
    try {
      ringbacktone.play();
    }
    catch (e) {}
  }

  function stopRingbackTone() {
    try {
      ringbacktone.pause();
    }
    catch (e) {}
  }

  function getUniqueID() {
    return Math.random().toString(36).substr(2, 9);
  }

  function newSession(session) {
    session.displayName = session.remoteIdentity.displayName || session.remoteIdentity.uri.user;
    session.fluxid = getUniqueID();
    var status;
    if (session.direction === 'Inbound') {
      status = "Entrada: " + session.displayName;
      console.log("CHAMADA DE ENTRADA");
      startRingTone();
    }
    else if (session.direction === 'Transfer') {
      status = "Chamando: " + session.displayName;
      console.log("TRANSFERENCIA");
      console.log("URI RID: " + session.remoteIdentity.uri.user);
      console.log("DYSPLAY NAME: " + session.displayName);
      console.log("SESSION: " + session.fluxid);
      startRingbackTone();
    }
    else {
      status = "Chamando: " + session.displayName;
      console.log("CHAMADA DE SAIDA");
      console.log("URI LOCAL: " + session.localIdentity.uri.user);
      console.log("URI REMOTE: " + session.remoteIdentity.uri.user);
      console.log("DYSPLAY NAME: " + session.displayName);
      console.log("SESSION: " + session.fluxid);
      numero = session.remoteIdentity.uri.user;
      console.log("NUMERO: " + numero);
      numero = numero;
      startRingbackTone();
    }
    logCall(session, 'ringing');
    setCallSessionStatus(status);
    // EVENT CALLBACKS
    session.on('progress', function(e) {
      if (e.direction === 'outgoing') {
        setCallSessionStatus('Discando...');
      }
    });
    session.on('connecting', function(e) {
      if (e.direction === 'outgoing') {
        setCallSessionStatus('Conectando...');
      }
    });
    session.on('accepted', function(e) {
      // If there is another active call, hold it
      if (callActiveID && callActiveID !== session.id) {
        phoneHoldButtonPressed(callActiveID);
      }
      stopRingbackTone();
      stopRingTone();
      setCallSessionStatus('Atendida');
      logCall(session, 'answered');
      callActiveID = session.id;
      User = user.User;
    });
    session.on('hold', function(e) {
      callActiveID = null;
      logCall(session, 'holding');
    });
    session.on('unhold', function(e) {
      logCall(session, 'resumed');
      callActiveID = session.id;
    });
    session.on('muted', function(e) {
      Sessions[session.fluxid].isMuted = true;
      setCallSessionStatus("Mudo");
    });
    session.on('unmuted', function(e) {
      Sessions[session.fluxid].isMuted = false;
      setCallSessionStatus("Atendida");
    });
    session.on('cancel', function(e) {
      stopRingTone();
      stopRingbackTone();
      setCallSessionStatus("Cancelada");
      if (this.direction === 'outgoing') {
        callActiveID = null;
        session = null;
        logCall(session, 'ended');
      }
    });
    session.on('bye', function(e) {
      stopRingTone();
      stopRingbackTone();
      setCallSessionStatus("");
      logCall(session, 'ended');
      callActiveID = null;
      session = null;
    });
    session.on('failed', function(e) {
      stopRingTone();
      stopRingbackTone();
      setCallSessionStatus("");
    });
    session.on('rejected', function(e) {
      stopRingTone();
      stopRingbackTone();
      setCallSessionStatus('Rejeitada');
      callActiveID = null;
      logCall(session, 'ended');
      session = null;
    });
    Sessions[session.fluxid] = session;
  }

