  function Stopwatch(elem, options) {
    // private functions
    function createTimer() {
      return document.createElement("span");
    }
    var timer = createTimer(),
      offset, clock, interval;
    // default options
    options = options || {};
    options.delay = options.delay || 1000;
    options.startTime = options.startTime || Date.now();
    // append elements
    elem.appendChild(timer);

    function start() {
      if (!interval) {
        offset = options.startTime;
        interval = setInterval(update, options.delay);
      }
    }

    function stop() {
      if (interval) {
        clearInterval(interval);
        interval = null;
      }
    }

    function reset() {
      clock = 0;
      render();
    }

    function update() {
      clock += delta();
      render();
    }

    function render() {
      timer.innerHTML = moment(clock).format('mm:ss');
    }

    function delta() {
      var now = Date.now(),
        d = now - offset;
      offset = now;
      return d;
    }
    // initialize
    reset();
    // public API
    this.start = start; //function() { start; }
    this.stop = stop; //function() { stop; }
  }

