    $password.val(localStorage.getItem('webPhonePassword') || '');
    $logLevel.val(localStorage.getItem('webPhoneLogLevel') || logLevel);

    $form.on('submit', function(e) {
      console.log('Normal Flow');

      e.preventDefault();
      e.stopPropagation();

      login(
        $server.val(),
        $appKey.val(),
        $appSecret.val(),
        $login.val(),
        $ext.val(),
        $password.val(),
        $logLevel.val()
      );
    });
    //
