sipHangUp : function(sessionid) {

            var s = fluxSip.Sessions[sessionid];
            // s.terminate();
            if (!s) {
                return;
            } else if (s.startTime) {
                s.bye();
            } else if (s.reject) {
                s.reject();
            } else if (s.cancel) {
                s.cancel();
            }

        },

sipSendDTMF : function(digit) {

            try { fluxSip.dtmfTone.play(); } catch(e) { }

            var a = fluxSip.callActiveID;
            if (a) {
                var s = fluxSip.Sessions[a];
                s.dtmf(digit);
            }
        },

