phoneMuteButtonPressed : function (sessionid) {

            var s = fluxSip.Sessions[sessionid];

            if (!s.isMuted) {
                s.mute();
            } else {
                s.unmute();
            }
        },

phoneHoldButtonPressed : function(sessionid) {

            var s = fluxSip.Sessions[sessionid];

            if (s.isOnHold().local === true) {
                s.unhold();
            } else {
                s.hold();
            }
        },


